              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "Last 1 year",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              end.setTime(end.getTime() + 3600 * 1000 * 24 * 1); //tomorrow
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      todayData: {},
      taxData: [],
      selected: [],
      topSoldOut: [],
      saleData: [],
      noOfSaleData: [],
      profitData: [],
      purchasedData: [],

      receivedAmountChartData: [],
