            shadeIntensity: 1,
            type: "horizontal",
            opacityFrom: 1,
            opacityTo: 1,
            stops: [0, 100, 100, 100],
          },
        },
        stroke: {
          show: true,
          curve: "smooth",
          width: 5,
        },
        xaxis: {
          type: "datetime",
