              return val + "¥";
            },
          },
        },
      },

      purchasedChartOptions: {
        chart: {
          type: "line",
          zoom: {
            enabled: false,
          },

          animations: {
            enabled: true,
            easing: "ease-in-out",
            dynamicAnimation: {
              speed: 1000,
            },
          },
          dropShadow: {
            enabled: true,
            top: 12,
            left: 0,
            blur: 4,
            opacity: 0.15,
            color: ["#fc9842"],
          },
        },
        plotOptions: {},
        colors: ["#fe5f75"],
        dataLabels: {
          enabled: false,
        },
        fill: {
          type: "gradient",
          gradient: {
            shade: "dark",
            gradientToColors: ["#fc9842"],
            shadeIntensity: 1,
            type: "horizontal",
            opacityFrom: 1,
            opacityTo: 1,
            stops: [0, 100, 100, 100],
          },
        },
        stroke: {
          show: true,
          curve: "smooth",
          width: 5,
        },
        xaxis: {
          type: "datetime",
          categories: [],
        },
        yaxis: {
          title: {
            text: `Profit Amount`,
          },
        },
        tooltip: {
          x: {
            format: "dd-MM-yyyy",
          },
          y: {
            formatter: function (val) {
              return val + "¥";
            },
          },
        },
      },

      today: null,
    };
  },
  watch: {
    dates() {
      this.getData();
    },
  },
  computed: {
    ...mapState(["isReady", "user", "store"]),
  },
  created() {
    this.$store.commit("setIsReady", false);
    setTimeout(() => {
      const end = new Date();
      const start = new Date();

      start.setTime(start.getTime() - 3600 * 1000 * 24 * 10); //last 10 days
      end.setTime(end.getTime() + 3600 * 1000 * 24 * 1); //tomorrow
      this.today = this.$moment().format("YYYY-MM-DD");
      this.dates = [
        this.$moment(start).format("YYYY-MM-DD"),
        this.$moment(end).format("YYYY-MM-DD"),
      ];
    }, 300);
  },
  methods: {
    getData() {
      this.getting = true;
      axios
        .get(
          `sale-reports/?from=${this.dates[0]}&to=${this.dates[1]}&today=${this.today}`
        )
        .then((response) => {
          var saleReport = response.data;
          this.todayData = saleReport.today_data;
          var labels = [];
          var ordered_amount = [];
          var received_amounts = [];
          var sale_counts = [];
          var profit_amounts = [];
          var purchased_amounts = [];
          saleReport.total_sales.forEach((res, index) => {
            if (index !== saleReport.total_sales.length - 1) {
              labels.push(res.label);
              res.data.forEach((data) => {
                if (data.type == "sale_price") {
                  ordered_amount.push(data.data);
                }
                if (data.type == "received_amounts") {
                  received_amounts.push(data.data);
                }
                if (data.type == "sale_counts") {
                  sale_counts.push(data.data);
                }

                if (data.type == "profit_amount") {
                  profit_amounts.push(data.data);
                }
                if (data.type == "purchased_amount") {
                  purchased_amounts.push(data.data);
                }
              });
            }
          });

          this.saleData = [
            {
              name: "Ordered amount",
              data: ordered_amount,
            },
            {
              name: "Received amount",
              data: received_amounts,
            },
          ];
          this.noOfSaleData = [
            {
              name: "Number of orders",
              data: sale_counts,
            },
          ];
          this.profitData = [
            {
              name: "Profit Amount",
              data: profit_amounts,
            },
          ];
          this.purchasedData = [
            {
              name: "Purchased Amount",
              data: purchased_amounts,
            },
          ];
          this.saleChartOptions.xaxis.categories = labels;
          this.noOfSaleChartOptions.xaxis.categories = labels;
          this.profitChartOptions.xaxis.categories = labels;
          this.purchasedChartOptions.xaxis.categories = labels;
          this.getting = false;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.getting = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
  },
};
</script>

